"use client";
import { Company } from "@/types/company";
import { Options } from "@/types/filter";
import React, { useState } from "react";
import { ChevronDown, ChevronUp } from "lucide-react";

export default function MultiFilterDropDown({
  options,
  filterData,
  categoryName,
  onFilterChange,
}: {
  categoryName: string;
  options: Options;
  filterData: Company[] | string[];
  onFilterChange: (selectedValues: string[]) => void;
}) {
  const { isObject, option } = options;
  const [isOpen, setIsOpen] = useState(false);
  const [selectedValues, setSelectedValues] = useState<string[]>([]);

  const toggleDropdown = () => setIsOpen(!isOpen);

  const handleCheckboxChange = (value: string) => {
    setSelectedValues((prevSelected) => {
      const updatedSelection = prevSelected.includes(value)
        ? prevSelected.filter((v) => v !== value) // Remove if already selected
        : [...prevSelected, value]; // Add new selection

      onFilterChange(updatedSelection); // Pass updated selection to parent
      return updatedSelection;
    });
  };

  return (
    <div className="w-full max-w-md mx-auto relative">
      {/* Toggle Button */}
      <button
        onClick={toggleDropdown}
        className="w-full flex items-center justify-between bg-gray-100 px-4 py-2 rounded-md shadow-sm text-left text-sm font-medium text-gray-700 hover:bg-gray-200 focus:outline-none"
        aria-expanded={isOpen}
      >
        <span>{categoryName}</span>
        {isOpen ? <ChevronUp size={16} /> : <ChevronDown size={16} />}
      </button>

      {/* Dropdown Menu */}
      {isOpen && (
        <div className="mt-2 p-4 bg-white border rounded-md shadow-md max-h-64 overflow-y-auto space-y-2 absolute z-10 w-full">
          {filterData?.map((item, index) => {
            let optionLabel = "";
            let optionValue = "";

            if (isObject) {
              optionLabel = (item as Record<string, any>)[
                option?.label ?? "name"
              ];
              optionValue = (item as Record<string, any>)[
                option?.value ?? "id"
              ];
            } else {
              optionLabel = item as string;
              optionValue = item as string;
            }

            const uniqueKey = `${optionValue}-${index}`;

            return (
              <label
                key={uniqueKey}
                className="flex items-center gap-2 cursor-pointer text-sm"
              >
                <input
                  type="checkbox"
                  value={optionValue}
                  checked={selectedValues.includes(optionValue)}
                  onChange={() => handleCheckboxChange(optionValue)}
                  className="form-checkbox h-4 w-4 text-blue-600"
                />
                <span className="truncate">{optionLabel}</span>
              </label>
            );
          })}
        </div>
      )}
    </div>
  );
}
