"use client";
import { Filter, Options } from "@/types/filter";
import MultiFilterDropDown from "./dropdown";
import { Company } from "@/types/company";
import { useState } from "react";

export default function Filters({
  filter,
  setSelectedFilters,
}: {
  filter: Filter;
  setSelectedFilters: React.Dispatch<
    React.SetStateAction<{
      company: string[];
      location: string[];
      employmentType: string[];
    }>
  >;
}) {
  const filterCategories: Options[] = [
    {
      name: "Company Name",
      key: "company",
      isObject: true,
      option: {
        label: "name",
        value: "name", // Using company name for filtering
      },
    },
    {
      name: "Location",
      key: "location",
    },
    {
      name: "Employment Type",
      key: "employmentType",
    },
  ];

  const handleFilterChange = (
    categoryKey: string,
    selectedValues: string[]
  ) => {
    setSelectedFilters((prevFilters) => ({
      ...prevFilters,
      [categoryKey]: selectedValues,
    }));
  };

  return (
    <div className="flex flex-wrap mx-auto justify-around align-middle my-10 p-3 bg-white border-1 border-slate-200 w-3/4">
      {filterCategories.map((category) => {
        // Ensure type safety with proper dynamic key access
        const rawData = filter[category.key as keyof Filter];

        // Type checking the rawData based on the category
        const data =
          Array.isArray(rawData) &&
          rawData.every(
            (item) =>
              typeof item === "string" ||
              (typeof item === "object" && item !== null && "name" in item)
          )
            ? (rawData as string[] | Company[])
            : [];

        return (
          <div key={category.key} className="flex flex-col mb-2">
            <h2 className="text-lg font-semibold font-sans text-slate-600 mb-2">
              {category.name}
            </h2>
            <div className="flex flex-col">
              <MultiFilterDropDown
                options={category}
                filterData={data}
                categoryName={category.name}
                onFilterChange={(selectedValues) =>
                  handleFilterChange(category.key, selectedValues)
                }
              />
            </div>
          </div>
        );
      })}
    </div>
  );
}
